import React, { Component } from "react";
import Carousel from "@brainhubeu/react-carousel";
import "@brainhubeu/react-carousel/lib/style.css";
import imageOne from "../../image/imageSlider/2b24d495052a8ce66358eb576b8912c8.jpg";
import imageTwo from "../../image/imageSlider/dfaa3e6eb2d9b31fcee6f23c248f30fe.jpg";
import imageThree from "../../image/imageSlider/unnamed.jpg";

import style from "./stylename.module.scss";

export default class MyCarousel extends Component {
  render() {
    return (
      <div className={style.container}>
        <Carousel
          arrows
          slidesPerPage={5}
          slidesPerScroll={2}
          animationSpeed={1200}
          autoPlay={3000}
          stopAutoPlayOnHover
          offset={30}
          itemWidth={250}
          clickToChange
          centered
        >
          <img src={imageOne} />
          <img src={imageTwo} />
          <img src={imageThree} />
        </Carousel>
      </div>
    );
  }
}
